import { Component, NgZone, OnInit } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { Marker } from '../models/marker';
import { MarkerService } from '../services/marker.service';
import { element } from 'protractor';
import { LocalstorageService } from '../services/localstorage.service';
import { Storage } from '@ionic/storage';
import { ModalController } from '@ionic/angular';
import { MapMarker } from '../models/map-marker';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { NativeGeocoder, NativeGeocoderResult, NativeGeocoderOptions } from '@ionic-native/native-geocoder/ngx';


declare var google;

@Component({
  selector: 'app-map-markers',
  templateUrl: './map-markers.page.html',
  styleUrls: ['./map-markers.page.scss'],
})
export class MapMarkersPage implements OnInit {

  marker = {} as MapMarker;
  aMarker : Marker[];
  markers: MapMarker[] = [];

  map = null;

  address: string;
  latitude: number;
  longitude: number;
  autocomplete: {input:string;};
  autocompleteItems: any[];
  location: any;
  placeid: any;
  GoogleAutocomplete: any;
  myLatLng = {};

  constructor(public atrCtrl: AlertController, private markerservice: MarkerService,
    private localstorageservice:LocalstorageService, private storage: Storage,
    private modalCtrl: ModalController, private geolocation: Geolocation,
    private nativeGeocoder: NativeGeocoder,
    public zone: NgZone) { }

  async getMarkers(){
    console.log("Invocando os Markers ");
    let result = await this.markerservice.getAllMarkers().toPromise();
    result.forEach((values) => {
      this.marker = {
        position: {
          lat: values["addressLocalization"].x,
          lng: values["addressLocalization"].y,
        },
        title: values["companyName"]
      }    
      this.markers.push(this.marker);
    })
    console.log(this.markers);
  }

  ngOnInit() { 
    //this.showMarkers();
  }

  ngAfterViewInit() {
    console.log("afterinit");
    setTimeout(() => {
      //console.log(this.mapRef.nativeElement);
      this.loadMap();
    }, 1000);
  }

  loadMap() {
    /*const mapEle: HTMLElement = document.getElementById('map');
    this.geolocation.getCurrentPosition().then((resp) => {
      this.latitude = -20.839217;//resp.coords.latitude;
      this.longitude = -41.128867;//resp.coords.longitude;
  
      this.myLatLng = new google.maps.LatLng(this.latitude, this.longitude);
      let mapOptions = {
        center: this.myLatLng,
        zoom: 15
      }
      
      //this.getAddressFromCoords(resp.coords.latitude, resp.coords.longitude);

      this.map = new google.maps.Map(mapEle, mapOptions);
    }).catch((error) => {
      console.log('Error getting location', error);
    });*/
    // create a new map by passing HTMLElement
    this.latitude = -20.839217;//resp.coords.latitude;
    this.longitude = -41.128867;//resp.coords.longitude;
    
    this.geolocation.getCurrentPosition().then((resp) => {
      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;
    }).catch(()=>{
      this.latitude = -20.839217;//resp.coords.latitude;
      this.longitude = -41.128867;//resp.coords.longitude;
    });

    const mapEle: HTMLElement = document.getElementById('map');
    // create LatLng object
    const myLatLng = {lat: this.latitude, lng: this.longitude};
    // create map
    this.map = new google.maps.Map(mapEle, {
      center: myLatLng,
      zoom: 12
    });

    google.maps.event.addListenerOnce(this.map, 'idle', () => {
      this.renderMarkers();
      mapEle.classList.add('show-map');
    });
  }

  async showMarkers(){
    await this.getMarkers();
  }

  async callMarkers(){
    await this.getMarkers();
    this.loadMap()
  }

  renderMarkers() {
    this.markers.forEach(mark => {
      this.addMarker(mark);
    });
  }

  addMarker(marker: MapMarker) {
    return new google.maps.Marker({
      position: marker.position,
      map: this.map,
      title: marker.title
    });
  }
  

}
