import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { Marker } from '../models/marker';
import { HTTP } from '@ionic-native/http/ngx';
import { environment } from 'src/environments/environment';
import { LocalstorageService } from './localstorage.service';
import { LoaderService } from './loader.service';

@Injectable({
  providedIn: 'root'
})
export class MarkerService {

  constructor(private httpClient: HttpClient, private nativeHTTP: HTTP,
    private localstorageservice:LocalstorageService, private loaderservice:LoaderService) { }

  httpOpions = {};

  getAllMarkers(): Observable<Marker[]>{   
    //console.log(token);

    this.  httpOpions = {
      headers: new HttpHeaders({'Content-Type':'application/json'})
    }

    return this.httpClient.get<Marker[]>(environment.apiMarker, this.httpOpions)
    .pipe(
      retry(2),
      catchError(this.handleError)
    )
  }
  

  // Manipulação de erros
  handleError(error: HttpErrorResponse) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Erro ocorreu no lado do client
      errorMessage = error.error.message;
    } else {
      // Erro ocorreu no lado do servidor
      errorMessage = `Código do erro: ${error.status}, ` + `menssagem: ${error.message}`;
    }
    console.log(errorMessage);
    return throwError(errorMessage);
  }
}
